import React, { Component } from 'react';
import * as firebase from 'firebase';
import './App.css';

class App extends Component {
  state = {
    messages: [],
    value: '',
    itsMe: false
  }

  componentDidMount() {
    let dbRef = firebase.database().ref();
    dbRef.on('child_added', (snap) => {
      this.setState((prev, next) => {
        return {
          messages: [
            ...prev.messages,
            {
              itsMe: prev.itsMe,
              message: snap.val()
            }
          ],
          itsMe: false
        }
      })
    });
  }

  handleInputChange = (e) => {
    this.setState({
      value: e.target.value
    })
  }

  handleSend = () => {
    this.setState({
      itsMe: true
    });
    let dbRef = firebase.database().ref();
    this.state.value ? dbRef.push(this.state.value) : console.log('Do not simply press flight icon..!');
    this.setState({
      value: ''
    });
  }

  render() {
    console.log(this.state);
    return (
      <div>
        <Chat messages={this.state.messages} />
        <Input value={this.state.value} onChange={this.handleInputChange} onClick={this.handleSend} />
      </div>
    );
  }
}

const Chat = (props) => {
  return (
    <div className="screen">
      {props.messages.map((el, indx) => {
        return <div key={indx} className={el.itsMe ? "itsMe" : "chat"}><span className="msg">{el.message}</span></div>
      })}
    </div>
  )
}

const Input = (props) => {
  return (
    <div className="input">
      <input value={props.value} onChange={props.onChange} type="text" name="msg" id="msg" />
      <button onClick={props.onClick}>✈</button>
    </div>
  )
}

export default App;



