import React from 'react';
import ReactDOM from 'react-dom';
import * as firebase from 'firebase';
import App from './App';
import registerServiceWorker from './registerServiceWorker';

// Initialize Firebase
const config = {
    apiKey: "AIzaSyCIgildxUl62uaQo4oT6Yt7mY9X5X9xxt8",
    authDomain: "realtime-chat-8273e.firebaseapp.com",
    databaseURL: "https://realtime-chat-8273e.firebaseio.com",
    projectId: "realtime-chat-8273e",
    storageBucket: "realtime-chat-8273e.appspot.com",
    messagingSenderId: "567772031477"
};
firebase.initializeApp(config);

window.onbeforeunload = (e) => {
    console.log('Unmounting...');
    let rootRef = firebase.database().ref('/');
    rootRef.set(null);
};

ReactDOM.render(<App />, document.getElementById('root'));
registerServiceWorker();



